:root {
  font-family: Inter, system-ui, Avenir, Helvetica, Arial, sans-serif;
  line-height: 1.5;
  font-weight: 400;

  color-scheme: light dark;
  color: rgba(255, 255, 255, 0.87);
  background-color: #242424;

  font-synthesis: none;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  min-width: 320px;
  min-height: 100vh;
  overflow-x: hidden;
  background: linear-gradient(135deg, #0a0a0a 0%, #1a1a1a 50%, #0a0a0a 100%);
}

#app {
  width: 100vw;
  min-height: 100vh;
}

.container {
  position: relative;
  width: 100%;
  min-height: 100vh;
}

.hero-section {
  position: relative;
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

#scene-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  pointer-events: none;
}

.content {
  position: relative;
  z-index: 10;
  padding: 2rem;
  text-align: center;
  pointer-events: none;
}

.title {
  font-size: 4em;
  line-height: 1.1;
  margin-bottom: 0.5rem;
  background: linear-gradient(45deg, #646cff, #9f9fff);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-shadow: 0 0 30px rgba(100, 108, 255, 0.3);
  animation: glow 2s ease-in-out infinite alternate;
}

@keyframes glow {
  from {
    text-shadow: 0 0 30px rgba(100, 108, 255, 0.3);
  }
  to {
    text-shadow: 0 0 40px rgba(100, 108, 255, 0.6), 0 0 60px rgba(100, 108, 255, 0.3);
  }
}

.subtitle {
  font-size: 1.4em;
  color: rgba(255, 255, 255, 0.8);
  margin-bottom: 1rem;
  text-shadow: 0 0 20px rgba(255, 255, 255, 0.2);
}

.scroll-hint {
  font-size: 1.1em;
  color: rgba(255, 255, 255, 0.6);
  font-style: italic;
  animation: pulse 2s ease-in-out infinite;
  text-shadow: 0 0 10px rgba(255, 255, 255, 0.3);
}

@keyframes pulse {
  0%, 100% { opacity: 0.6; }
  50% { opacity: 1; }
}

.scroll-sections {
  position: relative;
  z-index: 5;
  background: rgba(0, 0, 0, 0.3);
  backdrop-filter: blur(10px);
}

/* Resume Section Styles */
.resume-section {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 6rem 2rem;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  position: relative;
  overflow: hidden;
}

.resume-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: radial-gradient(circle at center, rgba(100, 108, 255, 0.1) 0%, transparent 70%);
  opacity: 0;
  transition: opacity 0.5s ease;
  pointer-events: none;
}

.resume-section:hover::before {
  opacity: 1;
}

.section-content {
  max-width: 1200px;
  width: 100%;
  position: relative;
  z-index: 2;
}

.section-content h2 {
  font-size: 3em;
  margin-bottom: 3rem;
  text-align: center;
  background: linear-gradient(45deg, #646cff, #9f9fff);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-shadow: 0 0 30px rgba(100, 108, 255, 0.3);
}

/* About Section */
.about-content {
  text-align: center;
  max-width: 900px;
  margin: 0 auto;
}

.profile-info h3 {
  font-size: 2.5em;
  margin-bottom: 0.5rem;
  color: #646cff;
  text-shadow: 0 0 20px rgba(100, 108, 255, 0.4);
}

.profile-info .title {
  font-size: 1.5em;
  margin-bottom: 2rem;
  color: rgba(255, 255, 255, 0.9);
  text-shadow: 0 0 15px rgba(255, 255, 255, 0.2);
}

.profile-info .summary {
  font-size: 1.3em;
  line-height: 1.8;
  color: rgba(255, 255, 255, 0.8);
  font-style: italic;
  text-shadow: 0 0 10px rgba(255, 255, 255, 0.1);
}

/* Skills Section */
.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 3rem;
  margin-top: 3rem;
}

.skill-category h3 {
  font-size: 1.5em;
  margin-bottom: 1.5rem;
  color: #646cff;
  text-align: center;
  text-shadow: 0 0 15px rgba(100, 108, 255, 0.3);
}

.skill-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.8rem;
  justify-content: center;
}

.skill-tag {
  background: linear-gradient(45deg, #646cff, #535bf2);
  color: white;
  padding: 0.7rem 1.2rem;
  border-radius: 25px;
  font-size: 1em;
  font-weight: 500;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(100, 108, 255, 0.3);
  backdrop-filter: blur(10px);
}

.skill-tag:hover {
  transform: translateY(-3px) scale(1.05);
  box-shadow: 0 8px 25px rgba(100, 108, 255, 0.5);
}

/* Experience Timeline */
.timeline {
  position: relative;
  max-width: 900px;
  margin: 0 auto;
}

.timeline::before {
  content: '';
  position: absolute;
  left: 50%;
  top: 0;
  bottom: 0;
  width: 3px;
  background: linear-gradient(to bottom, #646cff, #9f9fff);
  transform: translateX(-50%);
  box-shadow: 0 0 20px rgba(100, 108, 255, 0.5);
}

.timeline-item {
  position: relative;
  margin-bottom: 4rem;
  display: flex;
  align-items: center;
}

.timeline-item:nth-child(odd) {
  flex-direction: row;
}

.timeline-item:nth-child(even) {
  flex-direction: row-reverse;
}

.timeline-marker {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 20px;
  height: 20px;
  background: #646cff;
  border-radius: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
  box-shadow: 0 0 0 6px rgba(100, 108, 255, 0.2), 0 0 20px rgba(100, 108, 255, 0.5);
  animation: pulse 2s ease-in-out infinite;
}

.timeline-content {
  width: 45%;
  padding: 2rem;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 15px;
  backdrop-filter: blur(15px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
  transition: all 0.3s ease;
}

.timeline-content:hover {
  transform: translateY(-5px);
  box-shadow: 0 12px 40px rgba(100, 108, 255, 0.2);
}

.timeline-content h3 {
  font-size: 1.4em;
  color: #646cff;
  margin-bottom: 0.5rem;
  text-shadow: 0 0 10px rgba(100, 108, 255, 0.3);
}

.timeline-content h4 {
  font-size: 1.2em;
  color: rgba(255, 255, 255, 0.9);
  margin-bottom: 0.5rem;
}

.timeline-meta {
  font-size: 0.9em;
  color: rgba(255, 255, 255, 0.6);
  margin-bottom: 1rem;
}

.timeline-description {
  list-style: none;
}

.timeline-description li {
  position: relative;
  padding-left: 1.5rem;
  margin-bottom: 0.8rem;
  color: rgba(255, 255, 255, 0.8);
  line-height: 1.6;
}

.timeline-description li::before {
  content: '▸';
  position: absolute;
  left: 0;
  color: #646cff;
  font-size: 1.2em;
}

/* Education Section */
.education-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  gap: 3rem;
  margin-top: 3rem;
}

.education-item {
  padding: 2.5rem;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 15px;
  backdrop-filter: blur(15px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  text-align: center;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
  transition: all 0.3s ease;
}

.education-item:hover {
  transform: translateY(-5px);
  box-shadow: 0 12px 40px rgba(100, 108, 255, 0.2);
}

.education-item h3 {
  font-size: 1.4em;
  color: #646cff;
  margin-bottom: 0.5rem;
  text-shadow: 0 0 10px rgba(100, 108, 255, 0.3);
}

.education-item h4 {
  font-size: 1.2em;
  color: rgba(255, 255, 255, 0.9);
  margin-bottom: 0.5rem;
}

.education-meta {
  font-size: 0.9em;
  color: rgba(255, 255, 255, 0.6);
  margin-bottom: 0.5rem;
}

.gpa {
  font-size: 0.9em;
  color: #646cff;
  font-weight: 600;
  text-shadow: 0 0 10px rgba(100, 108, 255, 0.3);
}

/* Projects Section */
.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  gap: 3rem;
  margin-top: 3rem;
}

.project-card {
  padding: 2.5rem;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 15px;
  backdrop-filter: blur(15px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  transition: all 0.3s ease;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
}

.project-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 15px 50px rgba(100, 108, 255, 0.3);
}

.project-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 1.5rem;
}

.project-header h3 {
  font-size: 1.4em;
  color: #646cff;
  margin-bottom: 0.5rem;
  text-shadow: 0 0 10px rgba(100, 108, 255, 0.3);
}

.project-link {
  color: #646cff;
  text-decoration: none;
  font-size: 0.9em;
  font-weight: 500;
  transition: color 0.3s ease;
  text-shadow: 0 0 10px rgba(100, 108, 255, 0.3);
}

.project-link:hover {
  color: #9f9fff;
}

.project-description {
  color: rgba(255, 255, 255, 0.8);
  margin-bottom: 1.5rem;
  line-height: 1.7;
  font-size: 1.1em;
}

.project-tech {
  display: flex;
  flex-wrap: wrap;
  gap: 0.8rem;
  margin-bottom: 1.5rem;
}

.tech-tag {
  background: rgba(100, 108, 255, 0.2);
  color: #646cff;
  padding: 0.5rem 1rem;
  border-radius: 20px;
  font-size: 0.9em;
  font-weight: 500;
  border: 1px solid rgba(100, 108, 255, 0.3);
}

.project-features {
  list-style: none;
}

.project-features li {
  position: relative;
  padding-left: 1.8rem;
  margin-bottom: 0.8rem;
  color: rgba(255, 255, 255, 0.7);
  font-size: 1em;
  line-height: 1.6;
}

.project-features li::before {
  content: '✓';
  position: absolute;
  left: 0;
  color: #646cff;
  font-weight: bold;
  font-size: 1.2em;
}

/* Certifications Section */
.certifications-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  margin-top: 2rem;
}

.certification-item {
  padding: 2rem;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 12px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  text-align: center;
  transition: all 0.3s ease;
}

.certification-item:hover {
  transform: translateY(-3px);
  box-shadow: 0 6px 20px rgba(100, 108, 255, 0.2);
}

.certification-item h3 {
  font-size: 1.2em;
  color: #646cff;
  margin-bottom: 0.5rem;
}

.cert-issuer {
  color: rgba(255, 255, 255, 0.9);
  margin-bottom: 0.5rem;
}

.cert-date {
  color: rgba(255, 255, 255, 0.6);
  margin-bottom: 1rem;
}

.cert-link {
  color: #646cff;
  text-decoration: none;
  font-size: 0.9em;
  font-weight: 500;
  transition: color 0.3s ease;
}

.cert-link:hover {
  color: #9f9fff;
}

/* Contact Section */
.contact-info {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  margin-top: 3rem;
}

.contact-item {
  display: flex;
  align-items: center;
  gap: 1.5rem;
  padding: 2rem;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 15px;
  backdrop-filter: blur(15px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  transition: all 0.3s ease;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
}

.contact-item:hover {
  transform: translateY(-5px);
  box-shadow: 0 12px 40px rgba(100, 108, 255, 0.2);
}

.contact-icon {
  font-size: 2em;
  min-width: 40px;
  filter: drop-shadow(0 0 10px rgba(255, 255, 255, 0.3));
}

.contact-item a {
  color: #646cff;
  text-decoration: none;
  transition: color 0.3s ease;
  font-size: 1.1em;
  text-shadow: 0 0 10px rgba(100, 108, 255, 0.3);
}

.contact-item a:hover {
  color: #9f9fff;
}

.contact-item span {
  color: rgba(255, 255, 255, 0.8);
  font-size: 1.1em;
}

/* Responsive Design */
@media (max-width: 768px) {
  .title {
    font-size: 2.5em;
  }
  
  .section-content h2 {
    font-size: 2.2em;
  }
  
  .timeline::before {
    left: 20px;
  }
  
  .timeline-item {
    flex-direction: row !important;
  }
  
  .timeline-marker {
    left: 20px;
  }
  
  .timeline-content {
    width: calc(100% - 60px);
    margin-left: 60px;
  }
  
  .skills-grid,
  .education-grid,
  .projects-grid {
    grid-template-columns: 1fr;
  }
  
  .contact-info {
    grid-template-columns: 1fr;
  }
}

@media (prefers-color-scheme: light) {
  :root {
    color: #213547;
    background-color: #ffffff;
  }
  body {
    background: linear-gradient(135deg, #f5f5f5 0%, #e5e5e5 50%, #f5f5f5 100%);
  }
  .scroll-sections {
    background: rgba(255, 255, 255, 0.3);
  }
  .resume-section {
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  }
  .section-content h2 {
    background: linear-gradient(45deg, #646cff, #535bf2);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
  }
  .profile-info .summary,
  .project-description,
  .timeline-description li,
  .project-features li {
    color: rgba(0, 0, 0, 0.8);
  }
  .timeline-content,
  .education-item,
  .project-card,
  .contact-item {
    background: rgba(255, 255, 255, 0.2);
    border: 1px solid rgba(0, 0, 0, 0.1);
  }
  a:hover {
    color: #747bff;
  }
  button {
    background-color: #f9f9f9;
  }
}
