:root {
  color-scheme: light dark;
  color: #ffffffde;
  font-synthesis: none;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #242424;
  font-family: Inter, system-ui, Avenir, Helvetica, Arial, sans-serif;
  font-weight: 400;
  line-height: 1.5;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background: linear-gradient(135deg, #0a0a0a 0%, #1a1a1a 50%, #0a0a0a 100%);
  min-width: 320px;
  min-height: 100vh;
  margin: 0;
  overflow-x: hidden;
}

#app {
  width: 100vw;
  min-height: 100vh;
}

.container {
  width: 100%;
  min-height: 100vh;
  position: relative;
}

.hero-section {
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  display: flex;
  position: relative;
  overflow: hidden;
}

#scene-container {
  z-index: 1;
  pointer-events: none;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

.content {
  z-index: 10;
  text-align: center;
  pointer-events: none;
  padding: 2rem;
  position: relative;
}

.title {
  -webkit-text-fill-color: transparent;
  text-shadow: 0 0 30px #646cff4d;
  background: linear-gradient(45deg, #646cff, #9f9fff) text;
  margin-bottom: .5rem;
  font-size: 4em;
  line-height: 1.1;
  animation: 2s ease-in-out infinite alternate glow;
}

@keyframes glow {
  from {
    text-shadow: 0 0 30px #646cff4d;
  }

  to {
    text-shadow: 0 0 40px #646cff99, 0 0 60px #646cff4d;
  }
}

.subtitle {
  color: #fffc;
  text-shadow: 0 0 20px #fff3;
  margin-bottom: 1rem;
  font-size: 1.4em;
}

.scroll-hint {
  color: #fff9;
  text-shadow: 0 0 10px #ffffff4d;
  font-size: 1.1em;
  font-style: italic;
  animation: 2s ease-in-out infinite pulse;
}

@keyframes pulse {
  0%, 100% {
    opacity: .6;
  }

  50% {
    opacity: 1;
  }
}

.scroll-sections {
  z-index: 5;
  backdrop-filter: blur(10px);
  background: #0000004d;
  position: relative;
}

.resume-section {
  border-bottom: 1px solid #ffffff1a;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  padding: 6rem 2rem;
  display: flex;
  position: relative;
  overflow: hidden;
}

.resume-section:before {
  content: "";
  opacity: 0;
  pointer-events: none;
  background: radial-gradient(circle, #646cff1a 0%, #0000 70%);
  transition: opacity .5s;
  position: absolute;
  inset: 0;
}

.resume-section:hover:before {
  opacity: 1;
}

.section-content {
  z-index: 2;
  width: 100%;
  max-width: 1200px;
  position: relative;
}

.section-content h2 {
  text-align: center;
  -webkit-text-fill-color: transparent;
  text-shadow: 0 0 30px #646cff4d;
  background: linear-gradient(45deg, #646cff, #9f9fff) text;
  margin-bottom: 3rem;
  font-size: 3em;
}

.about-content {
  text-align: center;
  max-width: 900px;
  margin: 0 auto;
}

.profile-info h3 {
  color: #646cff;
  text-shadow: 0 0 20px #646cff66;
  margin-bottom: .5rem;
  font-size: 2.5em;
}

.profile-info .title {
  color: #ffffffe6;
  text-shadow: 0 0 15px #fff3;
  margin-bottom: 2rem;
  font-size: 1.5em;
}

.profile-info .summary {
  color: #fffc;
  text-shadow: 0 0 10px #ffffff1a;
  font-size: 1.3em;
  font-style: italic;
  line-height: 1.8;
}

.skills-grid {
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 3rem;
  margin-top: 3rem;
  display: grid;
}

.skill-category h3 {
  color: #646cff;
  text-align: center;
  text-shadow: 0 0 15px #646cff4d;
  margin-bottom: 1.5rem;
  font-size: 1.5em;
}

.skill-tags {
  flex-wrap: wrap;
  justify-content: center;
  gap: .8rem;
  display: flex;
}

.skill-tag {
  color: #fff;
  backdrop-filter: blur(10px);
  background: linear-gradient(45deg, #646cff, #535bf2);
  border-radius: 25px;
  padding: .7rem 1.2rem;
  font-size: 1em;
  font-weight: 500;
  transition: all .3s;
  box-shadow: 0 4px 15px #646cff4d;
}

.skill-tag:hover {
  transform: translateY(-3px)scale(1.05);
  box-shadow: 0 8px 25px #646cff80;
}

.timeline {
  max-width: 900px;
  margin: 0 auto;
  position: relative;
}

.timeline:before {
  content: "";
  background: linear-gradient(#646cff, #9f9fff);
  width: 3px;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  box-shadow: 0 0 20px #646cff80;
}

.timeline-item {
  align-items: center;
  margin-bottom: 4rem;
  display: flex;
  position: relative;
}

.timeline-item:nth-child(odd) {
  flex-direction: row;
}

.timeline-item:nth-child(2n) {
  flex-direction: row-reverse;
}

.timeline-marker {
  z-index: 2;
  background: #646cff;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  animation: 2s ease-in-out infinite pulse;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 0 0 6px #646cff33, 0 0 20px #646cff80;
}

.timeline-content {
  backdrop-filter: blur(15px);
  background: #ffffff1a;
  border: 1px solid #fff3;
  border-radius: 15px;
  width: 45%;
  padding: 2rem;
  transition: all .3s;
  box-shadow: 0 8px 32px #0000004d;
}

.timeline-content:hover {
  transform: translateY(-5px);
  box-shadow: 0 12px 40px #646cff33;
}

.timeline-content h3 {
  color: #646cff;
  text-shadow: 0 0 10px #646cff4d;
  margin-bottom: .5rem;
  font-size: 1.4em;
}

.timeline-content h4 {
  color: #ffffffe6;
  margin-bottom: .5rem;
  font-size: 1.2em;
}

.timeline-meta {
  color: #fff9;
  margin-bottom: 1rem;
  font-size: .9em;
}

.timeline-description {
  list-style: none;
}

.timeline-description li {
  color: #fffc;
  margin-bottom: .8rem;
  padding-left: 1.5rem;
  line-height: 1.6;
  position: relative;
}

.timeline-description li:before {
  content: "▸";
  color: #646cff;
  font-size: 1.2em;
  position: absolute;
  left: 0;
}

.education-grid {
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  gap: 3rem;
  margin-top: 3rem;
  display: grid;
}

.education-item {
  backdrop-filter: blur(15px);
  text-align: center;
  background: #ffffff1a;
  border: 1px solid #fff3;
  border-radius: 15px;
  padding: 2.5rem;
  transition: all .3s;
  box-shadow: 0 8px 32px #0000004d;
}

.education-item:hover {
  transform: translateY(-5px);
  box-shadow: 0 12px 40px #646cff33;
}

.education-item h3 {
  color: #646cff;
  text-shadow: 0 0 10px #646cff4d;
  margin-bottom: .5rem;
  font-size: 1.4em;
}

.education-item h4 {
  color: #ffffffe6;
  margin-bottom: .5rem;
  font-size: 1.2em;
}

.education-meta {
  color: #fff9;
  margin-bottom: .5rem;
  font-size: .9em;
}

.gpa {
  color: #646cff;
  text-shadow: 0 0 10px #646cff4d;
  font-size: .9em;
  font-weight: 600;
}

.projects-grid {
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  gap: 3rem;
  margin-top: 3rem;
  display: grid;
}

.project-card {
  backdrop-filter: blur(15px);
  background: #ffffff1a;
  border: 1px solid #fff3;
  border-radius: 15px;
  padding: 2.5rem;
  transition: all .3s;
  box-shadow: 0 8px 32px #0000004d;
}

.project-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 15px 50px #646cff4d;
}

.project-header {
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 1.5rem;
  display: flex;
}

.project-header h3 {
  color: #646cff;
  text-shadow: 0 0 10px #646cff4d;
  margin-bottom: .5rem;
  font-size: 1.4em;
}

.project-link {
  color: #646cff;
  text-shadow: 0 0 10px #646cff4d;
  font-size: .9em;
  font-weight: 500;
  text-decoration: none;
  transition: color .3s;
}

.project-link:hover {
  color: #9f9fff;
}

.project-description {
  color: #fffc;
  margin-bottom: 1.5rem;
  font-size: 1.1em;
  line-height: 1.7;
}

.project-tech {
  flex-wrap: wrap;
  gap: .8rem;
  margin-bottom: 1.5rem;
  display: flex;
}

.tech-tag {
  color: #646cff;
  background: #646cff33;
  border: 1px solid #646cff4d;
  border-radius: 20px;
  padding: .5rem 1rem;
  font-size: .9em;
  font-weight: 500;
}

.project-features {
  list-style: none;
}

.project-features li {
  color: #ffffffb3;
  margin-bottom: .8rem;
  padding-left: 1.8rem;
  font-size: 1em;
  line-height: 1.6;
  position: relative;
}

.project-features li:before {
  content: "✓";
  color: #646cff;
  font-size: 1.2em;
  font-weight: bold;
  position: absolute;
  left: 0;
}

.certifications-grid {
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  margin-top: 2rem;
  display: grid;
}

.certification-item {
  backdrop-filter: blur(10px);
  text-align: center;
  background: #ffffff0d;
  border: 1px solid #ffffff1a;
  border-radius: 12px;
  padding: 2rem;
  transition: all .3s;
}

.certification-item:hover {
  transform: translateY(-3px);
  box-shadow: 0 6px 20px #646cff33;
}

.certification-item h3 {
  color: #646cff;
  margin-bottom: .5rem;
  font-size: 1.2em;
}

.cert-issuer {
  color: #ffffffe6;
  margin-bottom: .5rem;
}

.cert-date {
  color: #fff9;
  margin-bottom: 1rem;
}

.cert-link {
  color: #646cff;
  font-size: .9em;
  font-weight: 500;
  text-decoration: none;
  transition: color .3s;
}

.cert-link:hover {
  color: #9f9fff;
}

.contact-info {
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  margin-top: 3rem;
  display: grid;
}

.contact-item {
  backdrop-filter: blur(15px);
  background: #ffffff1a;
  border: 1px solid #fff3;
  border-radius: 15px;
  align-items: center;
  gap: 1.5rem;
  padding: 2rem;
  transition: all .3s;
  display: flex;
  box-shadow: 0 8px 32px #0000004d;
}

.contact-item:hover {
  transform: translateY(-5px);
  box-shadow: 0 12px 40px #646cff33;
}

.contact-icon {
  filter: drop-shadow(0 0 10px #ffffff4d);
  min-width: 40px;
  font-size: 2em;
}

.contact-item a {
  color: #646cff;
  text-shadow: 0 0 10px #646cff4d;
  font-size: 1.1em;
  text-decoration: none;
  transition: color .3s;
}

.contact-item a:hover {
  color: #9f9fff;
}

.contact-item span {
  color: #fffc;
  font-size: 1.1em;
}

@media (width <= 768px) {
  .title {
    font-size: 2.5em;
  }

  .section-content h2 {
    font-size: 2.2em;
  }

  .timeline:before {
    left: 20px;
  }

  .timeline-item {
    flex-direction: row !important;
  }

  .timeline-marker {
    left: 20px;
  }

  .timeline-content {
    width: calc(100% - 60px);
    margin-left: 60px;
  }

  .skills-grid, .education-grid, .projects-grid, .contact-info {
    grid-template-columns: 1fr;
  }
}

@media (prefers-color-scheme: light) {
  :root {
    color: #213547;
    background-color: #fff;
  }

  body {
    background: linear-gradient(135deg, #f5f5f5 0%, #e5e5e5 50%, #f5f5f5 100%);
  }

  .scroll-sections {
    background: #ffffff4d;
  }

  .resume-section {
    border-bottom: 1px solid #0000001a;
  }

  .section-content h2 {
    -webkit-text-fill-color: transparent;
    background: linear-gradient(45deg, #646cff, #535bf2) text;
  }

  .profile-info .summary, .project-description, .timeline-description li, .project-features li {
    color: #000c;
  }

  .timeline-content, .education-item, .project-card, .contact-item {
    background: #fff3;
    border: 1px solid #0000001a;
  }

  a:hover {
    color: #747bff;
  }

  button {
    background-color: #f9f9f9;
  }
}
